<template>
  <div>
    <v-container>

      <div>
        <h1></h1>
      </div>

      <div class="mt-5">
        <v-simple-table style="margin: 0px auto; width: 100%">
          <thead>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          </thead>
          <tbody v-for="(item) in boardList" :key="item.id">
          <tr>
            <td>{{ item.id }}</td>
            <td><a @click="enterContent(item.id)">{{ item.title }}</a></td>
            <td>{{ item.userName }}</td>
            <td>{{ item.modifyDate }}</td>
          </tr>
          </tbody>
        </v-simple-table>
      </div>


      <Paging/>

      <v-row style="width: 60%; margin: 0px auto;">
        <v-col cols="3">
          <v-select class="text-sm-body-2" v-model="selected" v-bind:items="options" outlined dense></v-select>
        </v-col>
        <v-col cols="7">
          <v-text-field
              v-model="keyWord"
              placeholder=" "
              outlined
              dense
              append-icon="mdi-search-web"
              @click:append="search(selected, keyWord)"
          />
        </v-col>
        <v-col cols="2">
          <v-btn v-if="isAuthenticated" dark @click="writeBoard"></v-btn>
        </v-col>


      </v-row>
      <div>

      </div>
    </v-container>
  </div>

</template>

<script>
import store from "@/store";
import axios from 'axios';
import Paging from "@/components/board/Paging";

export default {
  components: {
    'Paging': Paging
  },
  computed: {
    isAuthenticated() {
      return store.getters.isAuthenticated
    },
  },
  data() {
    return {
      list: '',
      boardList: '',
      selected: 'titleContent',
      options: [
        {text: '+', value: 'titleContent'},
        {text: '', value: 'title'},
        {text: '', value: 'content'},
        {text: '', value: 'userName'}
      ],
      keyWord: ''
    }
  },
  created() {
    this.fetchData(0)
    this.$EventBus.$on('goPage', (pageNo, selected, keyWord) => {
      this.fetchData(pageNo, selected, keyWord);
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData(pageNo, selected, keyWord) {
      let url = '/api/board/list/' + pageNo;

      //   url
      if (selected != null && keyWord != null) {
        url += '?' + selected + '=' + keyWord;
      }
      axios.get(url)
          .then((res) => {

            this.boardList = res.data.list.content

            //Vuex Store   
            //axios  res.data(hashmap) key list  object data
            //  
            this.$store.commit('PAGING', {
              pageNumber: res.data.list.number,
              pageSize: res.data.list.size,
              totalPages: res.data.list.totalPages,
              totalElements: res.data.list.totalElements,
              selected: selected,
              keyWord: keyWord

            })
          })
          .catch((err) => {
            window.alert(err)
          })
    },
    enterContent(boardId) {
      this.$router.push('/content/' + boardId)
    },
    search(selected, keyWord) {
      if (this.selected && this.keyWord) {
        this.fetchData(0, selected, keyWord)
      } else {
        window.alert(" .")
      }

    },
    writeBoard() {
      this.$router.push('/write')
    }
  }
}

</script>

<style scoped>

</style>